.number-Wrapper {
  will-change: transform;
  grid-area: 2/2/4/4;
  display: grid;
  grid-template-columns: repeat(4, 25%);
  grid-template-rows: repeat(4, 25%);
  z-index: 5;
  margin-bottom: 4.5vw; }
  .theme-light .number-Wrapper {
    color: #000000; }
  .theme-dark .number-Wrapper {
    color: #ffffff; }
  .number-Wrapper .count-Wrapper {
    position: absolute;
    right: -15.5vw;
    top: -9vh;
    font-size: 0.7vw;
    width: 15vw;
    display: inline-flex;
    justify-content: space-around;
    align-items: center; }
    .number-Wrapper .count-Wrapper .count {
      margin: 0;
      font-size: 1.5vw; }
  .number-Wrapper .three {
    grid-area: 2/1/4/3;
    font-size: 5vw;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin-right: 1.5vw; }
  .number-Wrapper .five {
    grid-area: 2/3/4/5;
    font-size: 5vw;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin-left: 1vw; }
